@charset "UTF-8";
h1, h2, h3, h4, h5, h6, p, ul, dl, li, body {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff2") format("woff2");
  font-display: swap;
}
.title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.green-button {
  display: inline-block;
  width: calc(100% - 40px);
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68B738;
  padding-top: 11px;
  padding-bottom: 11px;
}

.catalog-link {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  padding-right: 40px;
  background: url("../img/icon-arrow.svg") 100% 62%/24px 12px no-repeat;
}

.page-body {
  min-width: 320px;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: #444444;
}

.main-header {
  position: relative;
  min-width: 320px;
  padding-top: 65px;
  background: url("../img/logo-mobile-text.svg") calc(50% + 1px) 23px/101px 18px no-repeat;
}

.main-header__nav {
  position: relative;
}

.main-header__logo {
  position: absolute;
  left: 21px;
  top: -55px;
}

.main-header__toggle {
  position: absolute;
  right: 20px;
  top: -43px;
  width: 24px;
  height: 16px;
  border: none;
  cursor: pointer;
  background: url("../img/burger.svg") center/24px 16px no-repeat;
}

.main-header__nav-list {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  margin: 0 auto;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  border-bottom: 1px solid #e6e6e6;
}

.main-header__nav-item {
  border-top: 1px solid #e6e6e6;
}

.main-header__nav-item-link {
  display: block;
  color: #000000;
  background-color: #ffffff;
  text-decoration: none;
  text-align: center;
  padding: 23px 12px 22px;
}

.welcome {
  margin: 0 auto 20px;
  text-align: center;
  color: #ffffff;
  background: url("../img/bg-welcome-can.png") calc(50% + 1px) 167px/280px 270px no-repeat, linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 302px, #ffffff 302px, #ffffff 480px), url("../img/bg-welcome-cat.jpg") center top/contain no-repeat;
}
.welcome__title {
  padding-top: 27px;
  margin-bottom: 25px;
  margin-left: 3px;
}
.welcome__text {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 302px;
}
.welcome__link {
  margin-left: 2px;
}

.programs {
  margin: 0 20px 21px;
}
.programs__list {
  max-width: 570px;
  margin: 0 auto;
}
.programs__item {
  padding: 35px 20px 20px;
  min-width: 240px;
}
.programs__item:not(:last-of-type) {
  margin-bottom: 21px;
}
.programs__item:nth-of-type(1) {
  background: url("../img/cat-sitting.svg") 23px 21px/50px 53px no-repeat, #f2f2f2;
}
.programs__item:nth-of-type(2) {
  background: url("../img/cat-walking.svg") 14px 23px/67px 50px no-repeat, #f2f2f2;
}
.programs__item-title {
  font-size: 24px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding-left: 72px;
  margin-bottom: 35px;
}
.programs__item-text {
  font-size: 14px;
  margin-left: 1px;
}
.programs__item-line {
  border-top: 1px solid #d9d9d9;
  margin-top: 22px;
}
.programs__item-link {
  display: inline-block;
  margin-top: 10px;
}

.details {
  max-width: 620px;
  min-width: 320px;
  margin: 0 auto 50px;
}
.details__title-container {
  width: 277px;
  margin: 0 auto;
}
.details__title {
  color: #000000;
  margin-bottom: 41.5px;
}
.details__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 0 20px;
}
.details__item {
  max-width: 280px;
  height: 60px;
}
.details__item:nth-of-type(1) {
  background: url("../img/details-1.svg") top left 1px/61px 60px no-repeat;
}
.details__item:nth-of-type(2) {
  background: url("../img/details-2.svg") top left 1px/61px 60px no-repeat;
}
.details__item:nth-of-type(3) {
  background: url("../img/details-3.svg") top left 1px/61px 60px no-repeat;
}
.details__item:nth-of-type(4) {
  background: url("../img/details-4.svg") top left 1px/61px 60px no-repeat;
}
.details__item-text {
  font-size: 14px;
  padding-left: 82px;
  margin-top: 1.5px;
}

.example {
  margin: 0 auto;
  padding-top: 26px;
  background-color: #eaeaea;
}
.example__container {
  width: 280px;
  margin: 0 auto;
}
.example__title {
  margin-bottom: 42px;
  margin-left: 1px;
}
.example__description {
  font-size: 14px;
  margin-bottom: 20px;
  margin-left: 1px;
}
.example__list {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  margin-bottom: 34px;
  text-align: center;
}
.example__item {
  position: relative;
  width: 122px;
  display: flex;
  flex-direction: column-reverse;
  border: 1px solid #CDCDCD;
  border-radius: 3px;
  padding-top: 15px;
  padding-bottom: 10px;
}
.example__item-title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -15px;
  margin: 0 auto;
  width: 77px;
  font-size: 12px;
  line-height: 12px;
  background-color: #EAEAEA;
}
.example__item-text {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: -0.05px;
  color: #000000;
  margin: -1px 0 7px 2px;
}
.example__total {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 21px;
}
.example__preview {
  margin-bottom: 39px;
  margin-left: 1px;
}

.main-footer {
  min-width: 320px;
  margin: 0 auto;
  margin-top: -3px; /* закрывает белую линию на картинке сверху */
  background-color: #f2f2f2;
}

.main-footer-contacts {
  background-color: #ffffff;
}
.main-footer-contacts__container {
  width: 279px;
  margin: 0 auto 26px;
  padding-top: 28px;
  display: flex;
  justify-content: space-between;
}
.main-footer-contacts__title {
  width: 142px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.main-footer-contacts__address {
  width: 139px;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
}
.main-footer-contacts__iframe {
  width: 100%;
}
.main-footer-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 36px 0 0;
  width: 100%;
}
.main-footer-links__logo {
  margin-bottom: 8px;
}
.main-footer-links__line {
  width: calc(100% - 40px);
  border: 1px solid #d9d9d9;
}
.main-footer-links__socials-list {
  display: flex;
}
.main-footer-links__socials-item {
  padding: 11px 0 8px;
}
.main-footer-links__socials-item:not(:last-of-type) {
  margin-right: 24px;
}
.main-footer-links__academy {
  width: calc(100% - 40px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0 10px;
}
.main-footer-links__academy-link-text {
  text-decoration: none;
}
.main-footer-links__academy-link-logo {
  margin: 1px -1px 0 0;
}